<template>
  <div class="container" style="margin: 15px 15px;">
    <!-- 页面名字 -->
    <el-row type="flex" justify="start" style=" font-size: 16px; color: #303133;">页面管理</el-row>
    <el-row type="flex" justify="end" style="margin-top: 10px;">
      <el-button plain size="small">
       <router-link :to="{ path: '/page/detail', query: { pageState: 1 }}"> 添加页面</router-link>
      </el-button>
    </el-row>
    <!-- 资讯列表 -->
    <el-row style="margin-top: 30px;">
      <el-table :data="tableData"
                stripe
                style="width: 100%"
                @row-dblclick="dbClick">
        <el-table-column prop="id"
                        label="页面id"
                        min-width="150">
        </el-table-column>
        <el-table-column prop="name"
                        label="页面名称"
                        min-width="150">
        </el-table-column>
        <el-table-column label="操作"
                        min-width="100">
          <template slot-scope="scope">
            <el-button @click="editHandleClick(scope.row)" type="text" size="small">编辑</el-button>
            <el-button  @click="deleteHandleClick(scope.row)" type="text" size="small">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-row>
    <el-row style="margin-top:30px;"  type="flex" justify="end">
      <el-pagination @size-change="handleSizeChange"
                     @current-change="handleCurrentChange"
                     :current-page="curPage"
                     :page-sizes="[10, 20, 30, 40]"
                     :page-size="10"
                     layout="total, sizes, prev, pager, next, jumper"
                     :total="total">
      </el-pagination>
    </el-row>
  </div>
</template>
<script>

export default {
  data () {
    return {
      tableData: [],
      curPage: 1,
      pageSize: 20,
      total: 0
    }
  },

  components: {
  },

  methods: {
    // 获取信息列表
    queryPageList () {
      let params = {
        page: this.curPage,
        per: this.pageSize
      }
      this.$http.pageList(params, 'get').then((res) => {
        let result = res.data.success

        this.tableData = result.pages || []
        this.curPage = result.page.current_page
        this.pageSize = result.page.per
        this.total = result.page.total_count
      })
    },

    // page大小改变
    handleSizeChange (page) {
      console.log('page大小改变', page)
      this.pageSize = page
    },

    // 当前page改变
    handleCurrentChange (page) {
      console.log('当前page改变', page)
      this.curPage = page
    },

    // 编辑操作
    editHandleClick (row) {
      this.$router.push({
        path: '/page/detail',
        query: {
          pageState: 2,
          page_id: row.id
        }
      })
    },

    // 双击进入详情
    dbClick (row) {
      this.$router.push({
        path: '/page/detail',
        query: {
          pageState: 3,
          page_id: row.id
        }
      })
    },

    // 删除操作
    deleteHandleClick (row) {
      this.$confirm('确定删除？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let params = {
          page_id: row.id
        }
        this.$http.detelePage(params, 'delete').then(res => {
          let result = res.data.success
          if (result) {
            this.$message({
              type: 'success',
              message: '删除成功!'
            })
            setTimeout(this.queryInfoList, 300)
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    }
  },
  mounted () {
    this.queryPageList()
  }
}
</script>

